export default {
	name: 'tabbar',
	props: {
		current: {
			type: Number,
			default: 0,
		},
		list: {
			type: Array,
			default: [],
		},
	},
	data() {
		return {
			
		}
	},
	methods: {
		handleItemClicked(index) {
			// this.current = index;
			this.$emit('change', {
				detail: {
					current: index,
				},
			});
		}
	},
	computed: {
		scrollLeft() {
			return (this.current - 2) * uni.upx2px(148);
		},
	},
}
